/*====================================================*/
body {
  font-family: "Roboto Condensed", sans-serif;
  font-size: 2rem; }

.contenedor {
  max-width: 120rem;
  width: 95%;
  margin: 0 auto; }

a {
  text-decoration: none; }

img {
  width: 100%; }

h1, h2, h3, h4 {
  margin: 0; }

h1 {
  font-size: 4.5rem; }

h2 {
  font-size: 4rem; }

h3 {
  font-size: 3.8rem; }

h4 {
  font-size: 3rem; }

.centrar-texto {
  text-align: center; }

.header {
  background-color: #ff7a00;
  color: #ffffff; }
  .header h2 {
    text-transform: uppercase;
    color: #ffffff; }

.header-contenido {
  margin: 0 3rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center; }
  @media (min-width: 1200px) {
    .header-contenido {
      display: grid;
      grid-template-columns: repeat(3, 1fr);
      gap: 1.66667rem; } }

.header-logo {
  display: flex;
  align-items: center;
  gap: 1.66667rem; }
  .header-logo img {
    width: 10rem; }

.buscador {
  margin: 1.66667rem 0; }
  .buscador input {
    border: none;
    padding: 9px 10px 9px 50px;
    outline: none;
    font-size: 1.8rem;
    border-radius: 1.5rem;
    background: #ffffff url("../img/iconSearch3.png") no-repeat 9px center;
    width: 25rem;
    cursor: pointer; }
    @media (min-width: 768px) {
      .buscador input {
        width: 50rem; } }

.Shop-profile {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 2rem; }

.footer {
  background-color: #ff7a00;
  color: #ffffff; }
  .footer h2 {
    text-transform: uppercase;
    color: #ffffff; }

.footer-contenido {
  display: flex;
  flex-direction: column;
  margin: 0 3rem;
  padding-top: 1.25rem; }
  @media (min-width: 768px) {
    .footer-contenido {
      flex-direction: row;
      gap: 15rem; } }

.footer-logo {
  display: flex;
  align-items: center;
  gap: 1.66667rem; }
  .footer-logo img {
    width: 10rem; }

.contacto {
  display: flex;
  flex-direction: column; }
  .contacto a {
    color: #ffffff; }
  .contacto .email, .contacto .ubicacion {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 1rem; }

.sobreNosotros {
  display: flex;
  flex-direction: column;
  align-items: center; }
  .sobreNosotros a {
    color: #ffffff; }

.redesSociales-iconos {
  margin-top: 1rem;
  display: flex;
  justify-content: center;
  gap: 1.66667rem; }
